import React from "react";
import CalendarHeatmap from 'react-calendar-heatmap';
import './styles.css';
import { connect } from "react-redux";
import moment from 'moment';
import Legend from "../maps/Legend";
import ReactTooltip from 'react-tooltip';

const getTooltipDataAttrs = (value) => {
    // Temporary hack around null value.date issue
    if (!value || !value.date) {
        return null;
    }
    // Configuration for react-tooltip
    return {
        "data-tip": `${value.date.toString().slice(0, 10)} has count: ${value.count}`
    };
};

const updateDataToTooltip = (data, startDate, endDate) => {

}


function Calendar(props) {
    let currentDate = moment().format('YYYY-MM-DD');
    const START_DATE = new Date('2021-03-01');
    const END_DATE = new Date(currentDate);
    let valuesData = dataFormatter(props.magData, new Date(START_DATE), END_DATE);
    return (
        <div>
            <CalendarHeatmap
                startDate={START_DATE}
                endDate={END_DATE}
                values={valuesData}
                tooltipDataAttrs={getTooltipDataAttrs}
                classForValue={(value) => {
                    if (!value) {
                        return 'color-empty';
                    }
                    return `color-scale-${value.count}`;
                }}
            />
            <Legend />
            <ReactTooltip />
        </div>
    )
}


function dataFormatter(unformattedData, startDate, endDate) {
    let formattedData = unformattedData.map((datum) => {
        return {
            "date": moment(new Date(datum.datetime)).format("YYYY-MM-DD"),
            "count": datum.pniFilename,
        }
    });
    for (let i = startDate; i <= endDate;)
    return formattedData;
}

export default connect(
    (state) => ({
        magData: state.magData.individualMagData,
    }),
)(Calendar);